<template>
  <div class="page">
    <div class="page__bd">
      <div class="weui-search-bar" style="border-top: none;background-color: #ff4f4d;">
        <div class="weui-search-bar__form">
          <div class="weui-search-bar__box">
            <icon class="weui-icon-search_in-box" type="search" size="14"></icon>
            <input type="text" class="weui-search-bar__input" placeholder="搜索" v-model="inputVal" :focus="inputShowed" @input="inputTyping" />
            <div class="weui-icon-clear" v-if="inputVal.length > 0" @click="clearInput">
              <icon type="clear" size="14"></icon>
            </div>
          </div>
          <label class="weui-search-bar__label" :hidden="inputShowed" @click="showInput">
            <icon class="weui-icon-search" type="search" size="14"></icon>
            <div class="weui-search-bar__text">搜索</div>
          </label>
        </div>
        <div class="weui-search-bar__cancel-btn" style="color: #ffffff;font-size: 14px;"  :hidden="!inputShowed" @click="hideInput">取消</div>
      </div>

      <!--搜索出来的内容-->
      <div class="weui-cells searchbar-result" v-if="inputVal.length > 10">
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本</div>
          </div>
        </navigator>
        <navigator url="" class="weui-cell" hover-class="weui-cell_active">
          <div class="weui-cell__bd">
            <div>实时搜索文本</div>
          </div>
        </navigator>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        inputShowed: true,
        inputVal: ""
      }
    },
    mounted() {
      wx.setNavigationBarTitle({
        title: ''
      });
    },
    methods: {
      showInput() {
        this.inputShowed = true;
      },
      hideInput() {
       wx.navigateBack();
      },
      clearInput() {
        this.inputVal = '';
      },
      inputTyping(e) {
        console.log(e);
        this.inputVal = e.mp.detail.value
      }
    }
  }
</script>

<style scoped>
  .searchbar-result {
    margin-top: 0;
    font-size: 14px;
  }
  .searchbar-result:before {
    display: none;
  }
  .weui-cell {
    padding: 12px 15px 12px 35px;
  }
</style>



